.contacts {
  &__form {
    margin-bottom: 32px;
  }

  &__field {
    margin: 0 0 16px;

    &--last {
      margin-bottom: 32px;
    }
  }

  &__info {
    margin: 0 0 24px;

  }

  &__info-title {
    font-weight: 400;
    font-size: 14px;
    line-height: 140%;
    color: #9fadc2;
    margin: 0 0 8px;
  }

  &__info-link {
    font-weight: 400;
    font-size: 16px;
    line-height: 140%;
    color: #3e3e3e;
    text-decoration: none;
    transition: color 300ms;

    &:hover {
      color: $colorHover;
    }

  }
}
